<template>
  <div class="header">
    <!-- 粒子效果 -->
    <vue-particles
      color="#dedede"
      :particleOpacity="0.7"
      :particlesNumber="80"
      shapeType="circle"
      :particleSize="4"
      linesColor="#dedede"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.4"
      :linesDistance="150"
      :moveSpeed="3"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
    ></vue-particles>
    <div class="logo">
      <img src="../assets/icons8_jack_64.png" />
      <span>Notepad</span>
    </div>
    <div class="nav-bar">
      <span class="all">
        <router-link to="/all">全部</router-link>
      </span>
      <span class="untreated">
        <router-link to="/untreated">待处理</router-link>
      </span>
      <span>
        <router-link to="/over">已完成</router-link>
      </span>
      <span>
        <router-link to="/timeline">时间轴</router-link>
      </span>
    </div>
    <!-- 弹出菜单 -->
    <a class="t-btn" @click="showTools">
      <span></span>
    </a>
    <!-- 添加文章 -->
    <router-link to="/add">
      <span class="btn-add"></span>
    </router-link>
  </div>
</template>

<script>
export default {
  components: {},
  methods: {
    showTools() {
      this.$emit("tools");
    }
  }
};
</script>

<style lang="less" scoped>
.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  background: #fff;
  z-index: 9999;
  height: 65px;
  padding: 10px;
  box-shadow: 0px 2px 2px #bbb;
  background: #fff no-repeat;
  background-size: cover;

  .logo {
    display: inline-block;
    float: left;
    height: 65px;
    img {
      display: inline-block;
      float: left;
      width: 65px;
      height: 65px;
    }
    span {
      display: inline-block;
      float: left;
      font-size: 25px;
      font-weight: bold;
      padding-top: 35px;
      margin-left: 15px;
    }
  }
  .nav-bar {
    height: auto;
    overflow: hidden;
    position: absolute;
    left: 12rem;
    padding-top: 35px;
    padding-left: 20px;
    span {
      display: inline-block;
      width: 5rem;
      text-align: center;
      a {
        display: inline-block;
        padding: 5px;
        transition: transfrom 2s;
      }
      a.active {
        border-bottom: 4px solid #81cde6;
      }
      a:hover {
        transform: translateY(-1px);
        border-bottom: 4px solid #81cde6;
      }
    }
    .particles-js-canvas-el {
      z-index: -999;
    }
  }
  .t-btn {
    position: absolute;
    right: 4rem;
    top: 2.8rem;
    width: 30px;
    height: 26px;
    cursor: pointer;
    background: url(https://img.icons8.com/dusk/30/000000/restaurant-menu.png)
      no-repeat center;
  }
  .t-btn:hover {
  }
  .btn-add {
    position: absolute;
    right: 0.8rem;
    top: 0.7rem;
    width: 50px;
    height: 50px;
    background: url(../assets/icon_add_hover.png) no-repeat center center;
    background-size: 30px;
    margin-top: 20px;
    transition: background;
  }
  .btn-add:hover {
    background-size: 70%;
  }
}
</style>



